import log from "./log";
export default function() {
  try {
    var sdp = prompt("Please enter the answer SDP");
    if (typeof sdp == "string" && sdp.length > 1) {
      log("21", "Creating a sesson description from the offer SDP");
      sdp = JSON.parse(sdp);
      yourRemoteSDP = new RTCSessionDescription(sdp);
    }
  } catch (e) {
    log("21", e.message);
  }
}
