import log from "./log";
export default function() {
  try {
    var candidate = prompt("Please enter the ICE candidate");

    if (typeof candidate != null && candidate.length > 0) {
      var iceCandidate = new RTCIceCandidate(JSON.parse(candidate));
      log("21", "Creating RTC Ice Candidate.");
      myPC.addIceCandidate(iceCandidate);
      log("22", "Adding ICE Candidate to RTCPeerConnection");
    }
  } catch (e) {
    console.log(e.name);
    log("22", e.message);
  }
}
