import log from "./log";
export default function() {
  try {
    myPC.setRemoteDescription(
      yourRemoteSDP,
      function() {
        log("22", "Remote description on the local machine initialized.");
      },
      function() {
        log("22", "Setting Remote description failed.");
      }
    );
  } catch (e) {
    log("22", e.message);
  }
}
