import log from "./log";
export default function() {
  try {
    log("17", "AnswerConstraints initiated.");

    yourPC.createAnswer(
      function(sdp) {
        yourSDP = new RTCSessionDescription(sdp);
        log("18", "Answer created. Copy of Session Description appended.");
        $(".yoursdp").html("");
        $(".yoursdp").append(JSON.stringify(yourSDP));
      },
      function(err) {
        log("18", "Answer creation failed:" + err);
      },
      {
        mandatory: yourman()
      }
    );
  } catch (e) {
    log("17", e.message);
  }
}
